import React from 'react';
import { FormattedIcon } from '@components/icons';
import { socialMedia } from '@config';
import styled from 'styled-components';
import { theme, mixins, media } from '@styles';
const { fontSizes, fonts } = theme;

const StyledContainer = styled.footer`
  ${mixins.flexCenter};
  flex-direction: column;
  padding: 15px;
  text-align: center;
  height: auto;
  min-height: 70px;
`;
const StyledSocial = styled.div`
  color: ${props => props.theme.colors.lightSlate};
  width: 100%;
  max-width: 270px;
  margin: 0 auto 10px;
  display: none;
  ${media.tablet`display: block;`};
`;
const StyledSocialList = styled.ul`
  ${mixins.flexBetween};
  padding: 0;
  margin: 0;
  list-style: none;
`;
const StyledSocialLink = styled.a`
  padding: 10px;
  svg {
    width: 20px;
    height: 20px;
  }
`;
const StyledMetadata = styled.div`
  font-family: ${fonts.SFMono};
  font-size: ${fontSizes.xs};
  line-height: 1;
`;
const StyledCredit = styled.div`
  color: ${props => props.theme.colors.lightSlate};
  padding: 10px;
  a {
    color: ${props => props.theme.colors.green};
    &:hover {
      text-decoration: underline;
    }
  }
`;

const Footer = () => (
  <StyledContainer>
    <StyledSocial>
      <StyledSocialList>
        {socialMedia &&
          socialMedia.map(({ name, url }, i) => (
            <li key={i}>
              <StyledSocialLink
                href={url}
                target="_blank"
                rel="nofollow noopener noreferrer"
                aria-label={name}
              >
                <FormattedIcon name={name} />
              </StyledSocialLink>
            </li>
          ))}
      </StyledSocialList>
    </StyledSocial>
    <StyledMetadata tabindex="-1">
      <StyledCredit>
        <div>&copy; {new Date().getFullYear()} Danang Haris Setiawan</div>
        <div style={{ marginTop: '5px', fontSize: '10px', opacity: 0.6 }}>
          Design inspired by{' '}
          <a href="https://brittanychiang.com" target="_blank" rel="nofollow noopener noreferrer">
            Brittany Chiang
          </a>
        </div>
      </StyledCredit>
    </StyledMetadata>
  </StyledContainer>
);

export default Footer;
